import React from "react";
import "./styles.css";
import juaniflix from "../../images/juaniflix.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExternalLinkAlt } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

export default function Juaniflix() {
  document.title = "Juani Sabbatini | Projects"
  return (
    <div className="Project">
      <img src={juaniflix} alt="Juaniflx" className="project-image" />
      <div className="project-info">
        <h3 className="project-text">
          Juaniflix it´s a fully completed Netflix clone created with React, TMDB Api, Swiper and Material UI.
            <br />
            <br />
          <span className="languages">React</span>
        </h3>
        <div className="buttons-project">
          <a href="https://github.com/juanisabba/juaniflix/" target="_blank" rel="noreferrer">
          See it on GitHub{" "}
          <FontAwesomeIcon
            icon={faGithub}
            aria-hidden="true"
            className="button-icon"
          />
        </a>
          <a href="https://juanisabba.github.io/juaniflix/" target="_blank" rel="noreferrer">
          See the Website{" "}
          <FontAwesomeIcon
            icon={faExternalLinkAlt}
            aria-hidden="true"
            className="button-icon"
          />
        </a>
        </div>
      </div>
    </div>
  );
}
