import React from "react";
import "./styles.css";
import portfolio from "../../images/portfolio.png";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faGithub } from "@fortawesome/free-brands-svg-icons";

export default function Portfolio() {
  document.title = "Juani Sabbatini | Projects"
  return (
    <div className="Project">
      <img src={portfolio} alt="Covid API" className="project-image" />
      <div className="project-info">
        <h3 className="project-text">
        This is my personal portfolio where are my projects and information about me.
          <br />
          <br />
          <span className="languages">React</span>
        </h3>
        <div className="buttons-project">
          <a href="https://github.com/juanisabba/my-app" target="_blank" rel="noreferrer">
            See it on GitHub{" "}
            <FontAwesomeIcon
              icon={faGithub}
              aria-hidden="true"
              className="button-icon"
            />
          </a>
        </div>
      </div>
    </div>
  );
}
